import React, {Component} from 'react';
import sampleAstros from '../constellation.json';

export default class VirtualSky extends Component {

  componentDidMount() {
    this.$sky = $(this.sky);
    $.virtualsky({
      'id': 'starmap',
      'projection': 'stereo',
      'ra': 83.8220833,
      'dec': -5.3911111,
      'ground': false,
      'constellations': true,
      'fov': 15,
      objects: sampleAstros
    })
  }

  render() {
    return (
        <div>
          <div ref={sky => this.sky = sky} id={"starmap"} style={styles.container}></div>
          <h1>{this.props.mapShow ?
            'True' : 'False'
          }</h1>
        </div>
    );
  }

}

const styles = {
  container: {
    height: 400,
    width: '100%'
  }
};



